'use strict';
const Web3 = require('web3');
export default function checkRPC(rpc){
    const web3 = new Web3(new Web3.providers.HttpProvider(rpc));
    const startTime = Date.now();//毫秒级事件戳
    //console.log('startTime',startTime)
    const result = web3.eth.getChainId().then((e)=>{      
      return Date.now() - startTime  
    })
    .catch((e)=>{
        return false
    })
    return result
}