import React, { useState } from "react"

import { 
  Grid, 
  Button, 
  TextField, 
  InputLabel, 
  FormControl, 
  Select, 
  MenuItem
 } from '@mui/material';

import { Delete } from '@mui/icons-material';

import {useTranslation} from 'react-i18next';
import { useTheme } from '@mui/material/styles'

import {getContractABI} from './../helper/etherscanUtils'
import {rebuildContractABI} from './../helper/chainUtils'
import CONFIG from 'config/config.json'

export default function ContractAddressMethodGroup(props) {
  const { t } = useTranslation()
  const theme = useTheme(); 
  const { chain, chainId } = CONFIG.NETWORK
  const [index, setIndex] = useState(1);
  const [methodList, setMethodList] = useState([]);  

  const paramChange = async (e) => {
    const contractAddress = e.target.value
    //console.log(contractAddress)
    if(props.web3.utils.isAddress(contractAddress)){
      const abi = await getContractABI(chain,contractAddress)
      if(abi) {
        const methodList = rebuildContractABI(props.web3, abi, true)
        //console.log(methodList)        
        parseMethodList(methodList)        
      }
    }
    props.paramChange(e)
  };

  const parseMethodList = ((methodList) => {
    let methods = []
    methodList.forEach((element, index) => {
      methods.push(
        <MenuItem key={index} value = {element.abiSignature}>{element.name}</MenuItem>
      )
    })
    //console.log("methods//////////",methods)
    setMethodList(methods)
  })

  const paramSelectChange = (e) => {
    props.paramSelectChange(e)
  };

  const deleteParam = ((e) => {
    props.deleteParam(e)
  })  

  React.useEffect(() => {
    //console.log('props.transactionDetail',props.transactionDetail)
    setIndex(props.index)
  }, [props]);

return (
  <Grid container spacing={1}>
    <Grid item xs={6}>
      <TextField
        fullWidth
        id={"T_" + index}
        label={ t('watchContract.contractAddress') + '( * )'}
        variant="outlined"
        size="small"
        value={props.contractAddress}
        onChange={paramChange}
      />
    </Grid>    
    <Grid item xs={3}>
      <FormControl fullWidth>
        <InputLabel id={"param_label" + index} size="small">{t('watchContract.method')}</InputLabel>
        <Select
          id={"param_select_" + index}
          name={"S_" + index}
          label= {t('watchContract.method')}
          labelId={"param_label" + index}
          size="small"
          value={props.selected}
          onChange={paramSelectChange}
        >
          <MenuItem value = {0} >{t('watchContract.noSelected')}</MenuItem>
          {methodList}
        </Select>
      </FormControl>
    </Grid>

    <Grid item xs={1}>
      <Button id={ index } variant="outlined" startIcon={<Delete />} onClick={deleteParam}>{t('watchContract.delParam')}</Button>
    </Grid>
</Grid>
);
}

