'use strict';
//accounts array
module.exports = (web3) => {
  //拆分十六进制数据
  const parseInput = (input) =>{
    let inputs = []
    const inputLen = input.length
    let slicePos = 0  //风格开始位置  
    let sliceLen = 10 //分割长度
    let paramNum = 0 //第几个分割

    inputs[0]= input.slice(0,10) //从第一个到第十个   

    slicePos += 10
    sliceLen = 64
    paramNum ++

    while(slicePos < inputLen){
      inputs[paramNum]= input.slice(slicePos,slicePos + sliceLen)
      slicePos = slicePos + sliceLen
      paramNum ++
    }

    //////console.log('inputs',inputs)

    return inputs;
  }

  const myTo32Bytes = (str) =>{
    if(str.length<64){
        var str='0'.repeat(64-str.length)+str;
    }
    if(str.length>64){
        var str=str.slice(0,63);
    }	
    return str;	
  }

    const checkTx = (tx,watchArray) => {
      //console.log('watchArray',watchArray)
        for(let i = 0; i < watchArray.length; i++ ){
            switch(watchArray[i].action){
                case 'transferEth':
                  if(watchArray[i].address == tx.from.toLowerCase()) return { action:'transferEth' }
                  break;
                case 'recieveEth': 
                  if(watchArray[i].address == tx.to.toLowerCase()) return  { action:'recieveEth' }                            
                  break;
                case 'transferToken':
                  if( watchArray[i].address == tx.from.toLowerCase() && watchArray[i].oppositeAddress == tx.to.toLowerCase() ){                    
                    if(tx.input){
                      const inputs = parseInput(tx.input)
                      if( inputs[0] = '0xa9059cbb' ) return { action:'transferToken' }                     
                    }
                  }                   
                  break;
                case 'recieveToken':
                  if( watchArray[i].oppositeAddress == tx.to.toLowerCase() ){                    
                    if(tx.input){
                      const inputs = parseInput(tx.input)
                      if( inputs[0] = '0xa9059cbb' && inputs[1].toLowerCase() == myTo32Bytes(watchArray[i].address.slice(2))) return  { action:'recieveToken' }                      
                    }
                  }                     
                  break;
                case 'transferNFT':
                    if( watchArray[i].address == tx.from.toLowerCase() && watchArray[i].oppositeAddress == tx.to.toLowerCase() ){                    
                        if(tx.input){
                          const inputs = parseInput(tx.input)
                          if( inputs[0] = '0x23b872dd' &&  (inputs[1].toLowerCase() == myTo32Bytes(watchArray[i].address.slice(2)))) { 
                            return   { action:'transferNFT' }  
                          }                     
                        }
                      }  
                  break;
                case 'recieveNFT':    
                if( watchArray[i].oppositeAddress == tx.to.toLowerCase() ){                    

                  } 
                  break;        
                case 'interactToContract':
                  if(watchArray[i].address == tx.from.toLowerCase() && watchArray[i].oppositeAddress == tx.to.toLowerCase()) return { action:'interactToContract' } 
                  break;  
                case 'beingInteracted'://合约账户
                  if(watchArray[i].address == tx.to.toLowerCase()) return { action:'beingInteracted' } 
                  break;
                case 'noSelected':
                  if(watchArray[i].address == tx.from.toLowerCase() || watchArray[i].address == tx.to.toLowerCase()) return  { action:'noSelected' } 
                  if(tx.input){
                    const inputs = parseInput(tx.input)
                    const inputsLowCase = inputs.map( e => e.toLowerCase())
                    if(inputsLowCase.includes(myTo32Bytes(watchArray[i].address.slice(2))))  return  { action:'noSelected' } 
                  }                  
                  break;        
                default:
                  return false
            }      
        }        
    }



    return {
      checkLastBlock: async (accounts) => {
            const accountsLower = accounts.map( account => account.toLowerCase())        
            let block = await web3.eth.getBlock('latest',true);
            //console.log(`[*] Searching block ${ block.number }...`);
            if (block && block.transactions) {
                //console.log(`[+] Transaction found on block`,block.transactions);
                for (let tx of block.transactions) {
                    let transaction = await web3.eth.getTransaction(tx);
                    if(transaction.from){
                      if (accountsLower.includes(transaction.from.toLowerCase())) {
                          //console.log(`[+] Transaction found on block ${ block.number }`);
                          //console.log({ address: transaction.from, value: web3.utils.fromWei(transaction.value, 'ether'), timestamp: new Date() });
                      }
                    }
                }
            }
            return 0
        },
        checkBlocks: async (start, end, accounts) => {
            const accountsLower = accounts.map( account => account.toLowerCase()) 
            for (let i = start; i < end; i++) {
                let block = await web3.eth.getBlock(i)
                //console.log(`[*] Searching block ${ i }`);
                if (block && block.transactions) {
                    for (let tx of block.transactions) {
                        let transaction = await web3.eth.getTransaction(tx);
                        if (accountsLower.includes(transaction.from.toLowerCase())) {
                            //console.log(`[+] Transaction found on block ${ lastBlockNumber }`);
                            //console.log({ address: transaction.from, value: web3.utils.fromWei(transaction.value, 'ether'), timestamp: new Date() });
                        }
                    }
                }
            }
        },
        //For WatchContract.js
        getActionsFromLastBlock: async (accounts,methods) => {
            const accountsLower = accounts.map( account => account.toLowerCase()) 
            const methodsLower = methods.map( method => method.toLowerCase().slice(0,10))
            //console.log(methodsLower) 
            let foundTransaction = []       
            let block = await web3.eth.getBlock('latest',true);
            //console.log(`[*] Searching block ${ block.number }...`);
            if (block && block.transactions) {
                //console.log(`[+] Transaction found on block`,block.transactions);
                for (let tx of block.transactions) {
                    if(tx.to){
                        if (accountsLower.includes(tx.to.toLowerCase())) {
                            //console.log(`[+] Transaction found on block`, tx);
                            //can empty
                            //这个逻辑可能有问题
                            if(methodsLower.length == 0){                                
                                foundTransaction.push( tx )
                            }else{
                              if(methodsLower.includes(tx.input.slice(0,10))){
                                foundTransaction.push( tx )                                
                              }                                
                            }

                        }
                    }                    
                }
            }
            return foundTransaction
        },
        //For WatchAddress.js
        getAddressActionsFromLastBlock: async (watchArray) => {
            let addrArr = []
            let oppositeAddrArr = []            
            //console.log('watchArray',watchArray);              
            watchArray.map((element) => {
              if(web3.utils.isAddress(element.address)){
                addrArr.push(element.address)
              }
              if(web3.utils.isAddress(element.oppositeAddress)){
                oppositeAddrArr.push(element.oppositeAddress)
              }                           
            })
            //console.log('addrArr',addrArr);           
            let foundTransactionArray = []       
            let block = await web3.eth.getBlock('latest',true);
            //console.log(`[*] Searching block ${ block.number }...`);
            if (block && block.transactions) {
                //console.log(`[+] Transaction found on block`,block.transactions);
                for (let tx of block.transactions) {
                    if(tx.to){
                        if (
                        addrArr.includes(tx.to.toLowerCase()) 
                        || addrArr.includes(tx.from.toLowerCase()) 
                        || oppositeAddrArr.includes(tx.to.toLowerCase())
                        || oppositeAddrArr.includes(tx.from.toLowerCase())                          
                        ) {
                            const result = checkTx(tx,watchArray)
                            //console.log('result',result)
                            if(result){
                              let foundTransaction = {...tx ,action:result.action}
                              //console.log(foundTransaction)
                              foundTransactionArray.push(foundTransaction)
                            }
                        }
                    }                    
                }
            }
            return foundTransactionArray
        }
    }
}
