import React, {  useState } from "react";
import { Grid,Button,TextField } from '@mui/material';
import { styled } from '@mui/material/styles';

import {useTranslation} from 'react-i18next'; 

import checkRPC  from  'helper/rpc'
import Snackbars from 'components/Snackbars'
import CONFIG from 'config/config.json'

export default function SetRPC(props) {
  const { t } = useTranslation()
  const [rpc, setRPC] = useState(CONFIG.NETWORK.RPC);
  const [snackbarsStatu, setSnackbarsStatu] = useState({
    isOpen:false,
    msg:''
  }); 

  const setRPCAction = (event) => {
    setRPC(event.target.value);
  };

  const changeRPCAction = () => {
    //check connect
    checkRPC(rpc).then((responseTime) =>{
     //console.log('responseTime//',responseTime) 
     if(responseTime == false){
      props.action.changeRPCAction({available:false,rpc:rpc,msg:`${t('setRPC.connectRPCFail')}`})
      setSnackbarsStatu({isOpen:true,msg:`${t('setRPC.connectRPCFail')}`})
     }else{
      props.action.changeRPCAction({available:true,rpc:rpc,msg:`${t('setRPC.connectRPCSuccess')} ${responseTime} mm`}) 
      setSnackbarsStatu({isOpen:true,msg:`${t('setRPC.connectRPCSuccess')} ${responseTime} mm`})            
     }     
    })
  };

  const clearRPC = () => {
    setRPC('');
  };  

  return (  
    <Grid container spacing={2}>
      <Grid item xs={12} sm ={6}>
        <TextField 
          fullWidth 
          id="rpc" 
          label={t('setRPC.node')} 
          variant="outlined"  
          size="small" 
          value={rpc}
          onChange={setRPCAction}
        />
      </Grid>
      <Grid item xs={12}  sm ={5}>
        <Button variant="contained"  onClick={changeRPCAction}>{t('setRPC.confirm')}</Button>
        <Button variant="outlined" onClick={clearRPC}  disabled = {rpc == ''} style={{marginLeft:5}}>{t('setRPC.clear')}</Button>
      </Grid> 
      
      <Grid item xs={12} key={snackbarsStatu.msg} style={{display:(snackbarsStatu.isOpen ? ' ' : 'none')}}>
        <Snackbars msg = {snackbarsStatu.msg}></Snackbars>
      </Grid>  
    </Grid>
  )
}
