const axios = require('axios').default;
import CONFIG from '../config/config.json'

async function getContractABI(chain, contractAddress) {
  const proxyTarget = CONFIG.GETABIADDRESS//转发到地址
  try{   
    //由于服务器使用过的是https协议，这里也要使用https
    const contractABI = await axios.get(proxyTarget,{
        params:{
          //不需要module: 'contract',
          //不需要action: 'getabi',
          address: contractAddress,
          //不需要apikey: scanApiKey
        }
      })
    .then(function (response) {
      // handle success
      //console.log('getContractABI response',response.data.result);
      return response.data.result;
    })
    .catch(function (error) {
      // handle error
      //console.log('getContractABI error',error);
    });
    //console.log('11111111abi',abi)
    //try convert json,find if have error
    const isValidJson = JSON.parse(contractABI)
    return contractABI;
  }catch(e){
    return 0
  }
}
  
  export { getContractABI }