import React, { useEffect, useState } from "react";

import {Grid,
  Button,
  IconButton
} from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';

const Web3 = require('web3');

import SetRPC from  'components/common/SetRPC'
import Snackbars from 'components/Snackbars'

import SettingsIcon from '@mui/icons-material/Settings';

import {useTranslation} from 'react-i18next'; 
import CONFIG from 'config/config.json'

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { gridSpacing } from 'store/constant';
import { useTheme } from '@mui/material/styles';
import BadgeAvatars from 'components/common/BadgeAvatars';
import ContractAddressMethodGroup from 'components/ContractAddressMethodGroup';
const checkBlock = require('utils/watch/transactionChecker')

const truncateLR = (input, lenL,lenR) =>
  input.length > lenL + lenR ? `${input.substring(0, lenL)}...${input.substring(input.length-lenR)}` : input;

let sOpen = false

const audioUrl = require('./../../beep.mp3')

let timer = null
let lastBlockNumber = 0

///////////////////////////////////////////////////////////////////////////////////////////////
 
function WatchContract() {  
  const {t, i18n} = useTranslation()
  const theme = useTheme()
  const {CHAIN,CHAINID} = CONFIG.NETWORK
  const [rpc, setRPC] = useState(CONFIG.NETWORK.RPC); 
  const [web3, setWeb3] = useState(new Web3(new Web3.providers.HttpProvider(rpc))) 
  const [snackbarsStatu, setSnackbarsStatu] = useState({
    isOpen:false,
    msg:''
  });  
  const [paramArray, setParamArray] = useState([
    {
      contractAddress: "",
      method: '0',
      selected: 0
    }
  ]);
   
  const changeRPCAction = (newRpc) => {
     if(newRpc.available){     
       setWeb3(new Web3(new Web3.providers.HttpProvider(newRpc.rpc))) 
       if(sOpen) {
        sOpen = false
        setIsRunning(sOpen)
       }      
     }     
  }; 

  const [isOpenDialogs, setOpenDialogs] = useState(false);  
  const [dialogObject, setDialog] = useState({
    type:'',
    msg:null
  });  

  const [isRunning, setIsRunning] = useState(false);   
  /////////////////////////////////
  const [nowBlocknumber,setNowBlocknuber] =  useState(0);

	useEffect(() => {
    sOpen = false    
		return () => {
      //清除定时器
			clearInterval(timer)
		}
	}, []);

  let watchInterval = null

  const startWatch = () => {
    if(!verifyFormForStart()){
      return
    }
    sOpen = !sOpen;//这个似乎多余，不过记得这个放组件内部无效，以后再试试,可能重新渲染的远古
    setIsRunning(sOpen)
    ////////console.log(`[+] sOpen ${sOpen}`)
    if(sOpen){
      doWatch()         
    }else{
      ////////console.log('watchInterval1',watchInterval)          
      clearInterval(timer)
      timer = null
      ////////console.log('watchInterval2',watchInterval)
      return      
    }
  }
  
  const doWatch = () => {
	  watch() 
    watchInterval = setInterval( watch, 3000)  
    timer = watchInterval  
  }  
  
  const watch = async () => {
    let addrArr = []
    let methodArr = []    
    paramArray.map((element) => {
      if(web3.utils.isAddress(element.contractAddress)){
        addrArr.push(element.contractAddress)
      }
      if(element.method != '' && element.method != '0' ){
        methodArr.push(element.method)
      }      
    })

    //有些操作没有日志 GetPastLogs取不到 需要扫区块
    const { getActionsFromLastBlock } = checkBlock(web3) 
    const newBlockNumber = await web3.eth.getBlockNumber()
    //console.log(newBlockNumber)
    setNowBlocknuber(newBlockNumber)
    if (lastBlockNumber < newBlockNumber){
      lastBlockNumber = newBlockNumber
      getActionsFromLastBlock(addrArr,methodArr)
      .then((res)=>{
        //console.log('getActionsFromLastBlock',res)
        if(res.length > 0){
          res.map( tx => {updateGridRows(tx)})
          playBeep()
        }
        
      })
    }    
  }

  const playBeep = () => {
    var audio  = new Audio(audioUrl);
    //audio.src = audioUrl.default 
    audio.play();
  } 

  //================================================================VerifyForm Data
  const [formErrors, setFormErrors] = useState({})  

  const setFormErrorsAction = (id,value) =>{
    setFormErrors((prev) => {
      let newValue = { ...prev, ...{[id]:value} }
      ////////console.log('newValue',newValue)
      return newValue
    })
  }

  const verifyFormForStart = () => {
    const checkWatchAddr = hasWatchAddr() 
    setFormErrorsAction('watchAddr',!checkWatchAddr) 

    if(!checkWatchAddr){
      setSnackbarsStatu({
        isOpen:true,
        msg:`${t('watchContract.pleaseSetTheWatchAddress')} ( Time: ${Date.now()} )`}
      ); 
      return false      
    }
    return true    
  } 

  const hasWatchAddr = () => { 
    return web3.utils.isAddress(paramArray[0].contractAddress)
  }  

  const paramSelectChange = (e) => {
    let index = e.target.name.slice(2)
    let value = e.target.value
    paramArray[index].method = value
    paramArray[index].selected = value    
    //console.log("paramArray==>",paramArray)
    setParamArray(paramArray)
    parseParamsArray()
  };

  const paramChange = (e) => {
    let index = e.target.id.slice(2)
    let value = ((e.target.value).trim()).toLowerCase()
    //console.log(index)
    paramArray[index].contractAddress = value
    setParamArray(paramArray)
    parseParamsArray()
  };

  const [paramsDOMArray, setParamsDOMArray] = useState([])
  const parseParamsArray = (() => {
    let params = []
    //console.log("paramArray==>", paramArray)
    paramArray.forEach((element, index) => {
      //console.log('element:',element);
      params.push(
        <Grid item key={index} xs={12}>
          <ContractAddressMethodGroup
            index = {index}
            selected = { paramArray[index].selected }
            method = {paramArray[index].type}
            contractAddress = {paramArray[index].value}
            paramSelectChange = {paramSelectChange}
            paramChange = {paramChange}
            deleteParam = {deleteParam} 
            web3 = {web3}   
          /> 
        </Grid>
      )
    })
    //console.log("params//////////",params)
    setParamsDOMArray(params)
  })

  const addParam = (() => {
    let paramObject = { contractAddress: '', method: '', selected: '0' }
    paramArray.push(
      paramObject
    )
    setParamArray(paramArray)
    parseParamsArray();
  })

  const deleteParam = ((e) => {
    //console.log("e.id",e.target.id)
    paramArray.splice(e.target.id, 1);
    setParamArray(paramArray)
    parseParamsArray();
  })

  useEffect(() => {
    parseParamsArray();
  }, []);
  //================================================================
  const [gridRows, setGridRows] = useState([])

  const columns = [
    { field: "id", headerName: t('watchContract.id'), width: 20  },
    { field: "address", headerName:  t('watchContract.contractAddress'), width: 200 },
    { field: "from", headerName: t('watchContract.from'), width: 140 },
    { field: "hash", headerName: t('watchContract.hash'), width: 200 },
    { field: "method", headerName: t('watchContract.method'), width: 200 }       
  ];

  const updateGridRows = async (tx) => {
    try {
      let accountIndex
      let method
      for(let i = 0; i< paramArray.length; i++ ){
        if(paramArray[i].contractAddress.toLowerCase() == tx.to.toLowerCase()){
          //console.log(paramArray[i].contractAddress.toLowerCase())
          accountIndex = i + 1 
          method = paramArray[i].selected ? paramArray[i].selected : 'No Selected'
          break;         
        }
      }

      setGridRows((prev) => [...prev,{
        id:prev.length, 
        address: accountIndex + ' :: ' + truncateLR(tx.to,6,6), 
        from: truncateLR(tx.from,6,6),
        hash: truncateLR(tx.hash,6,16),
        method: method       
      }])
    } catch (error) {
      console.log('error ==> ', error)       
    }
    setSnackbarsStatu({ isOpen: true, msg: `${t('watchContract.foundNewTx')} ${tx.hash}` });
    return       
  }

  const [openSet, setOpenSet] = useState(true);  

  const setOpenSetAction = ()=>{
    setOpenSet(!openSet)
  }

  return ( 
    <MainCard 
      title={t('watchContract.title')}
      secondary={
        <Grid item xs={6} style={{textAlign:'right',verticalAlign:'middle',paddingTop:6,paddingBottom:6}}>         
          {isRunning && <BadgeAvatars />}
          <Button 
            variant="contained" 
            onClick={startWatch} 
            color='secondary'
            sx={{borderRadius:10}}             
          >
            {isRunning ? t('followMint.startRunning'): t('followMint.start')}
          </Button>          
          <IconButton aria-label="settings"  onClick={setOpenSetAction} title={'click to open/close setting'}>
            <SettingsIcon/>
          </IconButton>
        </Grid> 
      }>
      <Grid container spacing={gridSpacing}>
        <Grid item xs={12}  style={{display: openSet ? '' : 'none'}}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <SetRPC action={{ 'changeRPCAction': changeRPCAction }} data={{ 'rpc': rpc }} />
                </Grid>
              </Grid>
            </Grid>

            <Grid item xs={12}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                {t('watchContract.contractAddress')}<Button sx ={{ml:2}} variant="outlined" onClick={addParam}>{t('watchContract.add')}</Button>
                </Grid>
              </Grid>
            </Grid>
            {paramsDOMArray}
          </Grid>
        </Grid>

        <Grid item xs={12} key={snackbarsStatu.msg} style={{display: (snackbarsStatu.isOpen ? ' ' : 'none') }}>
          <Snackbars msg={snackbarsStatu.msg}></Snackbars>
        </Grid>

        <Grid item xs={12} style={{ marginTop:2,  height: 300, width: "100%"}}>
          <DataGrid 
            rows={gridRows} 
            columns={columns}  
            style={{ 
              marginTop:10, 
              borderRadius:10, 
              border:'1px solid',
              borderColor:theme.palette.secondary.main
              }}
          />
        </Grid>

      </Grid>
    </MainCard> 
  );
}

export default WatchContract;